.category-list {
  list-style-type: none;
  margin-top: 10px;
}

.category-list li a {
  display: block;
  padding: 10px;
  border-radius: 2px;
}

.category-list li a.active {
  background: #c2da00;
  color: #fff;
  text-decoration: none;
  text-transform: capitalize;
  outline: none;
}
